.weather {
  &__settings-btn {
    display: block;
    margin-right: 0;
    margin-left: auto;
    cursor: pointer;
    background: transparent;
    border: none;
  }
  &__settings-img {
    width: 25px;
  }
  &__item {
    padding: 5px;
    margin: 5px 0;
    border-bottom: 1px solid #c0c0c0;
    &:last-child {
      border-bottom: none;
    }
  }
  &__icon {
    width: 80px;
  }
  &__city {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    p {
      font-size: 32px;
      line-height: 100%;
    }
  }
  &__data {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    &-item {
      display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    }
  }
  &__data-temp {
    width: 40px;
  }
}